import Modal from '@/components/x-modal/x-modal'

import { mapGetters } from 'vuex'

// 获取用户所有地址方法,删除地址方法
import {getProfileAllAddress,setDefaultAddress,removeAddress} from '@/network/getProfileData'
export default{
	components:{
		Modal
	},
	data(){
		return{
			show: false,//弹窗打开隐藏
			nodata:false,//暂无数据
			windowHeight:652,//屏幕高度
			addressList:[],
			removeIndex:''
		}
	},
	onNavigationBarButtonTap() {
		this.addAddressClick()//新增加地址
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	onShow() {
		if(this.isToken){
			// 获取用户所有收货地址
			this.userAddressList(this.isToken)
		}
		else{
			uni.navigateTo({
				url:"../../login/login"
			})
		}
	},
	computed:{
		...mapGetters(['isToken'])
	},
	methods:{
		//获取用户所有地址
		userAddressList(token){
			getProfileAllAddress(token)
				.then(res => {
					
					if(res.data.code == 200){
						this.addressList = res.data.data
						if(res.data.data == 0) this.nodata = true
					}
				})
		},
		
		
		// 设置默认收货地址
		isSetDefaultAddress(isDefalut,id,item){
			const val = parseInt(isDefalut)
			const that = this
			if(val != 1){
				uni.showModal({
					title:'是否设置为默认收货地址？',
					content:'',
					cancelText:"我再想想",
					cancelColor:'#333333',
					confirmColor:'#333333',
					confirmText:'确定',
					success(res) {
						if(res.confirm){
							that.setDefaultAddress(id,that.isToken,item)
						}
					}
					
				})
			}
		},
		// 设置用户的默认地址
		setDefaultAddress(id,token,item){
			setDefaultAddress(id,token)
			.then(res => {
				if(res.data.code == 200){
					uni.showToast({
						title:"设置成功",
						icon:'none'
					})
					// 全部地址为不默认
					this.allAddressNot(this.addressList)
					
					item.is_default = 1
				}
			})
		},
		//新增地址
		addAddressClick(){
			uni.navigateTo({
				url:'addAddress'
			})
		},
		allAddressNot(list){
			list.forEach(x => {
				x.is_default = 0
			})
		},	
		//删除
		delClick(index){
			this.show = true
			this.removeIndex = index
		},
		//确认删除
		delAddressClcik(){
			console.log(this.removeIndex)
			removeAddress(this.addressList[this.removeIndex].id,this.isToken)
				.then(res => {
					if(res.data.code == 200){
						this.addressList.splice(this.removeIndex,1)
					}
				})
		},
		show6() {
			
		},
		hide6() {
			this.modal6= false
		},
		handleClick6(e) {
			
		},
		
		//编辑
		editClick(item){
			this.$store.commit('setAddress',item)
			uni.navigateTo({
				url:"addAddress"
			})
		},
	}
}
